  import React, { useState } from 'react';
  import Grid from '@mui/material/Grid';
  import { Card, CardContent, CardMedia, Typography, Dialog, DialogTitle, DialogContent, DialogActions, IconButton } from '@mui/material';
  import CloseIcon from '@mui/icons-material/Close';
  import styles from '../Text.module.css'; //  CSS-

  export default function CartItem({ img, description, author, texnika, size}) {
    
    const [open, setOpen] = useState(false); //       

    const handleOpen = () => {
      setOpen(true);
    };

    const handleClose = () => {
      setOpen(false);
    };

    return (
      <Grid item xs='12' md='3'>
        <Card sx={{ 
            height: '100%', 
            width: '100%', 
            boxShadow: 1, //    
            borderRadius:'10px',
            '&:hover': { //   
              boxShadow: 5, //    
              cursor:'pointer'
            }
          }}>
          <CardMedia
            sx={{ height: 200}}
            image={img}
            title=""
            onClick={handleOpen} //       
          />
          <CardContent>
            <Typography variant='h6' sx={{fontFamily:'Playfair Display'}}>: {texnika}</Typography>
            <Typography variant='h6' sx={{fontFamily:'Playfair Display'}}>: {size}</Typography>
            <Typography variant='h6' sx={{fontFamily:'Playfair Display' }}>: {author}</Typography>
          </CardContent>
        </Card>
        <Dialog open={open} onClose={handleClose}>
          <DialogTitle className={styles.text}>  </DialogTitle>
          <DialogContent>
            <img src={img} alt=" " style={{ width: '100%', height: 'auto' }} />
            <Typography variant="body1" className={styles.text}>: {description}</Typography>
            <Typography variant="body1" className={styles.text}>: {author}</Typography>
          </DialogContent>
          <DialogActions>
            <IconButton
              sx={{
                position: 'absolute',
                top: 8,
                right: 8,
              }}
              onClick={handleClose}
            >
              <CloseIcon />
            </IconButton>
          </DialogActions>
        </Dialog>
      </Grid>
    );
  }
