import { TextField } from '@mui/material';
import React from 'react';

export default function Search(props) {
    const { value, onChange } = props;
    return (
        <TextField
            type='search'
            variant='standard'
            label=''
            fullWidth
            value={value}
            onChange={onChange}
            sx={{ mt: 2, height: '40px', width: '49%' }} //    
            placeholder='     '
        ></TextField>
    );
}
